import React, {useState} from "react";
import FilterItem from "../filter-item/filter-item";
import PropTypes from 'prop-types';
import { Button, Popover, PopoverBody, Collapse } from 'reactstrap';
import './filter-group.scss';

function FilterGroup({onGroupSaveChanges = ()=>{},
                         groupName = "empty",
                         navItems=[],
                         countChecked = 0,
                         isMobile=false,
                         isForMobile=false}) {
    const itemsList = [...navItems];
    const [groupState, setGroupState] = useState(itemsList);
    const [popoverOpen, setPopoverOpen] = useState(false);

    //when user click on item, but still not apply
    const onCheckItem = (id) => {
        const index = groupState.findIndex(elem => elem.id === id),
              before = groupState.slice(0, index),
              after = groupState.slice(index + 1),
              newItem = {...groupState[index]};
        //change status of changed item
        newItem.isChecked = !newItem.isChecked;

        const newArray = [...before, newItem, ...after];
        setGroupState(newArray)
    }

    //generate items
    const itemsArray = groupState.map((item) => {
        return(
            <FilterItem
                key={item.id + new Date().getTime()}
                itemId={item.id}
                title={item.title}
                isChecked={item.isChecked}
                onCheckItem={onCheckItem}/>
        )
    })

    const togglePopover = () => {
        setPopoverOpen(!popoverOpen);
        if(popoverOpen) {
            setGroupState([...navItems]);
        }
    }

    const closePopover = () => {
        setPopoverOpen(false);
        setGroupState([...navItems]);
    }

    //text for showing count of checked and applied items
    const checkedItems = countChecked > 0 ? `(${countChecked})` : '';

    const uniqueId = `popover-${groupName}-${isForMobile}`;

    const desktopItems = <div className='filter-group'>
        <Button id={ uniqueId }>
            {groupName} {checkedItems}
        </Button>
        <Popover placement="bottom"
                 isOpen={ popoverOpen }
                 target={ uniqueId }
                 trigger="legacy"
                 toggle={ () => togglePopover() }>
            <PopoverBody>
                <div className="items-wrapper">
                    {itemsArray}
                </div>
                <div className="buttons-wrapper">
                    <Button outline color="danger" onClick={() => closePopover()}>Cancel</Button>
                    <Button outline color="success" onClick={() => {
                        onGroupSaveChanges(groupName, groupState);
                    }}>
                        Apply
                    </Button>
                </div>
            </PopoverBody>
        </Popover>
    </div>

    const mobileItems = <div className="filter-group-mobile">
            <button className="mobile-collapse-toggle" onClick={()=>togglePopover()}>
                {groupName} {checkedItems}
                <span>{popoverOpen ? "-" : "+"}</span>
            </button>
            <Collapse isOpen={popoverOpen}>
                <div className="items-wrapper">
                    {itemsArray}
                </div>
                <div className="buttons-wrapper">
                    <Button outline color="danger" onClick={() => closePopover()}>Cancel</Button>
                    <Button outline color="success" onClick={() => {
                        onGroupSaveChanges(groupName, groupState);
                    }}>
                        Apply
                    </Button>
                </div>
            </Collapse>
        </div>

    return(
        <>
            {isMobile ? mobileItems : desktopItems}
        </>
    )
}

FilterGroup.propTypes = {
    onGroupSaveChanges : PropTypes.func,
    groupName : PropTypes.string,
    navItems : PropTypes.array,
    countChecked : PropTypes.number,
    isMobile: PropTypes.bool,
    isForMobile: PropTypes.bool
};

export default FilterGroup;