import React from "react";
import PropTypes from 'prop-types';
import {Button} from "reactstrap";
import './filter-result.scss';

function FilterResult({checkedItemsArray = {},
                          onRemoveChecked = ()=>{},
                          onRemoveAllChecked = ()=>{}}) {

    const filteredItemElement = (groupName ,{title, id}) => {
        return (
            <div key={id} className="filtered-item" data-group={groupName} data-id={id}>
                {title}
                <button className="delete"
                      onClick={(e) => deleteClick(e)}
                      data-group={groupName}
                      data-id={id}
                >âœ•</button>
            </div>
        )
    }

    const deleteClick = ({target: {dataset: {group, id}}}) => {
        onRemoveChecked(group, id)
    }

    //generate filtered items UI
    let filteredItemsUI = [];
    let clearAllButton = "";
    const updateFilteredItemsUi = (key) => {
        checkedItemsArray[key].forEach(elem => {
            filteredItemsUI.push(filteredItemElement(key, elem));
        })
    }
    for(let key in checkedItemsArray) {
        updateFilteredItemsUi(key);
    }

    //make user to know that nothing was applied
    if (filteredItemsUI.length === 0) {
        filteredItemsUI = "-none-";
    } else {
        clearAllButton = <Button color="danger" outline className="clear-all-btn" onClick={() => onRemoveAllChecked()}>Clear All</Button>
    }

    return(
        <div className='filter-result'>
            Applied Filters:
            <div className="results-wrapper">
                {filteredItemsUI}
            </div>
            {clearAllButton}
        </div>
    )
}

FilterResult.propTypes = {
    checkedItemsArray : PropTypes.object,
    onRemoveChecked : PropTypes.func,
    onRemoveAllChecked : PropTypes.func,
}

export default FilterResult;