import React, {useState} from "react";
import PropTypes from 'prop-types';
import "./filter-navigation.scss";
import FilterGroup from "../filter-group/filter-group";
import {Button, Popover, PopoverBody} from "reactstrap";

function FilterNavigation({filters = {},
                              onGroupSaveChanges = ()=>{}}) {
    const [moreItemsIsOpen, setMoreItemsIsOpen] = useState(false);
    //generate group items
    const listOfGroups = Object.keys(filters);
    let mobileItemsFull = [];
    let mobileItemsSmall = [];
    let iterator = 0;
    const generatedGroups = listOfGroups.map((item) => {
        let count = 0;
        iterator++;
        filters[item].forEach(el => {
            if(el.isChecked) {
                count++;
            }
        })
        const uniqueKey = item + new Date().getTime();

        //mobile nav
        if(iterator <= 2) {
            //push first full items
            mobileItemsFull.push(<FilterGroup key={uniqueKey + "mobile"}
                                          groupName={item}
                                          onGroupSaveChanges={onGroupSaveChanges}
                                          navItems={[...filters[item]]}
                                          countChecked={count}
                                          isMobile={false}
                                          isForMobile={true}/>)
        } else {
            //push sub nav
            mobileItemsSmall.push(<FilterGroup key={uniqueKey + "mobile2"}
                                          groupName={item}
                                          onGroupSaveChanges={onGroupSaveChanges}
                                          navItems={[...filters[item]]}
                                          countChecked={count}
                                          isMobile={true}
                                          isForMobile={true}/>)
        }

        //desktop nav
        return <FilterGroup key={uniqueKey}
                            groupName={item}
                            onGroupSaveChanges={onGroupSaveChanges}
                            navItems={[...filters[item]]}
                            countChecked={count}
                            isMobile={false}/>
    });

    const togglePopover = () => {
        setMoreItemsIsOpen(!moreItemsIsOpen);
    }

    mobileItemsFull.push(<div key={"popover-wrapper"}>
        <Button id={ `popover-more-items-popover` }>
            More Filters
        </Button>
        <Popover placement="bottom"
                 isOpen={ moreItemsIsOpen }
                 target={ `popover-more-items-popover` }
                 trigger="legacy"
                 toggle={ () => togglePopover() }>
            <PopoverBody>
                {mobileItemsSmall}
            </PopoverBody>
        </Popover>
    </div>)

    return(
        <nav className="filter-navigation">
            <div className="desktop-nav">
                {generatedGroups}
            </div>
            <div className="mobile-nav">
                {mobileItemsFull}
            </div>
        </nav>
    )
}

FilterNavigation.propTypes = {
    filters : PropTypes.object,
    onGroupSaveChanges : PropTypes.func
}

export default FilterNavigation;