import React, {useState} from "react";
import filterOptions from "./services/filterOptions";
import FilterResult from "./components/filter-result/filter-result";
import FilterNavigation from "./components/filter-navigation/filter-navigation";
import logo from './logo.svg';
import './App.css';

function App() {
    let updatedFilterArray = {};
        //add isChecked status for each item and set to false by default
    for (const property in filterOptions) {
        updatedFilterArray[property] = [];
        filterOptions[property].forEach((el, i) => {
            updatedFilterArray[property].push({
                ...el,
                isChecked: false
            })
        })
    }

    const [filters, setFilters] = useState({...updatedFilterArray});

    //save group event
    const onGroupSaveChanges = (group, newItems) => {
        let newArray = {...filters};
        newArray[group] = newItems;
        setFilters(newArray);
    }

    //generate list of checked items
    let savedCheckedItemsArray = {...filters};
    for ( const key in savedCheckedItemsArray) {
        savedCheckedItemsArray[key] = savedCheckedItemsArray[key].filter(el => el.isChecked === true);
    }

    //event for remove item from "checked and approved" list
    const onRemoveChecked = (groupName, id) => {
        let newArray = {...filters};
        const index = newArray[groupName].findIndex(el => el.id === id);
        newArray[groupName][index].isChecked = false;
        setFilters(newArray);
    }

    //event for remove all checked items
    const onRemoveAllChecked = () => {
        let newArray = {...filters};
        for ( const key in newArray ) {
            newArray[key].forEach((item, index) => {
                if(item.isChecked) {
                    newArray[key][index].isChecked = false;
                }
            })
        }
        setFilters(newArray);
    }

    return (
        <div className="App">
            <header className="App-header">
                <img src={logo} className="App-logo" alt="logo"/>
            </header>
            <nav className="groups-nav-wrapper">
                <FilterNavigation filters={{...filters}} onGroupSaveChanges={onGroupSaveChanges}/>
            </nav>
            <FilterResult onRemoveChecked={onRemoveChecked}
                          checkedItemsArray={savedCheckedItemsArray}
                          onRemoveAllChecked={onRemoveAllChecked}/>
        </div>
    );
}

export default App;
